* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
  font-size: 1rem;
  line-height: 1.5;
  font-family: sans-serif;
  word-wrap: break-word;
  overflow-wrap: break-word;
  color: black;
  margin: 2em;
}

h1, h2 {
  font-size: 2rem;
  text-align: center;
  font-weight: normal;
  padding: 0.5rem 0 0 0;
}

p {
  margin: 1em 0;
}

pre {
  padding: 0px;
  border: 0px;
  border-radius: 0px;
}

a {
  color: #4183c4;
  font-weight: 300;
  text-decoration: none;
}

a:hover {
  color: #3d85c6;
  text-decoration: underline;
}

.wrapper {
  height: 100%;
  display: flex;
  flex-direction: column;
}

.video,
.audio {
  display: none;
}

.controls {
  display: flex;

  min-width: 8rem;
  max-width: 10rem;
  margin-top: 2rem;
  margin-bottom: 0.5rem;
}

button {
  flex-grow: 1;
  height: 3.5rem;
  border: none;
  border-radius: 0.15rem;
  background: #fe3e09;
  margin-left: 2px;
  box-shadow: inset 0 -0.15rem 0 rgba(0, 0, 0, 0.2);
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #ffffff;
  font-weight: bold;
  font-size: 1rem;
}

button:hover, button:focus {
  outline: none;
  background: #fe3e09;
}

button:active {
  box-shadow: inset 0 1px 0 rgba(0, 0, 0, 0.2);
  line-height: 3rem;
}

button:disabled {
  pointer-events: none;
  background: lightgray;
}

button:first-child {
  margin-left: 0;
}

