.main-controls {
  padding: 0.5rem 0;
}

.audio__controls {
  margin-right: 1rem;
}

canvas {
  min-width: 200px;
  display: block;
  margin-bottom: 0.5rem;

  margin-top: 2rem;
}

#buttons {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

#buttons button {
  font-size: 1rem;
  padding: 1rem;
  width: calc(50% - 0.25rem);
}

section, article {
  display: block;
}

.audio__block {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

audio {
  width: 100%;
  display: block;
  margin: 1rem auto 0.5rem;
}

@media all and (min-width: 800px) {
  .wrapper {
    width: 90%;
    max-width: 1000px;
    margin: 0 auto;
  }
}
